// Table of Contents:

// Logo
// ==========================================================================

$logo-width:240px;


// Colors
// ==========================================================================

$body-color:transparent;

// 主色
$primary-color: #C184C5 !default;
$primary-color-light: #CF95D3 !default;
$primary-color-dark: #A368A7 !default;

// 輔色
$secondary-color: #FFEF6F !default;
$orang-color: #FCA518 !default;
$red-color: #E6623B !default;
$pink-color: #EF8EAD !default;
$purple-color: #742080 !default;
$green-color: #71a0b4 !default;

//中間色
$standard-ancent:#454545 !default;
$lighen-ancent:#666 !default;
$pale-ancent:rgba(0, 0, 0, 1) !default;

//連結顏色
$link-color:#E0A7B1;

//footer顏色
$footer-copyright-bg-color:$standard-ancent; // Global 
// ==========================================================================

// Media Query Ranges
$xsmall-screen-up: 481px !default;
$small-screen-up: 769px !default;
$medium-screen-up: 1024px !default;
$large-screen-up: 1281px !default;
$xsmall-screen: 480px !default;
$small-screen: 768px !default;
$medium-screen: 1023px !default;
$large-screen: 1280px !default;
$extral-large-screen: 1860px !default;

$small-and-up: "only screen and (min-width : #{$xsmall-screen-up})" !default;
$medium-and-up: "only screen and (min-width : #{$small-screen-up})" !default;
$large-and-up: "only screen and (min-width : #{$medium-screen-up})" !default;
$extra-large-and-up: "only screen and (min-width : #{$extral-large-screen})" !default;
$xsmall-and-down: "only screen and (max-width : #{$xsmall-screen})" !default;
$small-and-down: "only screen and (max-width : #{$small-screen})" !default;
$large-and-down: "only screen and (max-width : #{$large-screen})" !default;
$medium-and-down: "only screen and (max-width : #{$medium-screen})" !default;
$medium-only: "only screen and (min-width : #{$small-screen-up}) and (max-width : #{$medium-screen})" !default;
$small-medium-only: "only screen and (min-width :  560px and (max-width : #{$small-screen})" !default;


// Typography
// ==========================================================================
// 思源黑體
$font-stack: 'PingFang TC',
'Adobe Blank',
'Microsoft JhengHei',
sans-serif;
// 思源宋體
$font-family-notoserif: 'TimesNewRoman,Times New Roman,Times,Baskerville,Georgia',
'Noto Serif TC',
'Microsoft JhengHei',
serif;

// Header Styles
$h1-fontsize: 105 !default;
$h2-fontsize: 98 !default;
$h3-fontsize: 56 !default;
$h4-fontsize: 50 !default;
$h5-fontsize: 36 !default;
$h6-fontsize: 28 !default;
$h7-fontsize: 24 !default;
$body-fontsize:20 !default;
$smallsize:12 !default;
$button-fontsize:20 !default;
$footer-fontsize: 16 !default;


// Nav
// ==========================================================================
//sidenav 淺色版顏色
$sidenav-font-color: $standard-ancent !default;
$sidenav-bg-color: #fff !default;
$sidenav-li-hover:rgba(0, 0, 0, .1);
$sidenav-font-color-hover:$primary-color;

// 13. Navigation Bar
// ==========================================================================

$navbar-wrapper-width:$large-screen;
$navbar-background-color:#fff;
$navbar-height: 70px !default;
$navbar-line-height: $navbar-height !default;
$navbar-height-mobile: 76px !default;
$navbar-line-height-mobile: $navbar-height-mobile !default;
$navbar-font-size: 1.4rem !default;
$navbar-font-color: $purple-color !default;
$navbar-font-color-hover: $primary-color !default;
$navbar-background-hover: inherit !default;
$navbar-brand-font-size: 2.1rem !default;



// Grid
// ==========================================================================

$num-cols: 12 !default;
$gutter-width: 1.5rem !default;
$element-top-margin: $gutter-width/3 !default;
$element-bottom-margin: ($gutter-width*2)/3 !default;


// Footer
// ==========================================================================

$footer-font-color: #fff !default;
$footer-bg-color: $primary-color-light !default;
$footer-copyright-font-color: $lighen-ancent !default;


// Progress Bar
// ==========================================================================

$progress-bar-color: $secondary-color !default;



// Carousel
// ==========================================================================

$carousel-height: 844px !default;
$carousel-item-height: $carousel-height / 2 !default;
$carousel-item-width: $carousel-item-height !default;


// Spinners | Loaders
// ==========================================================================

$spinner-default-color: $primary-color !default;
$overlay-default-color:#fff !default;
